import React from "react";
const UserInfo = (props) => {
  let person = props.person;
  return (
    <div className="ui description">
      <div className="item">
        <p>
           : <b>{`${person.firstName} ${person.lastName}`}</b>
        </p>
      </div>
      <div className="item">
        <p>
          <b>: </b>
          <p>{person.description}</p>
        </p>
      </div>
      <div className="item">
        <p>
           : <b>{person.address?.streetAddress}</b>{" "}
        </p>
      </div>
      <div className="item">
        <p>
          : <b>{person.address?.city}</b>
        </p>
      </div>
      <div className="item">
        <p>
           / : <b>{person.address?.state}</b>
        </p>
      </div>
      <div className="item">
        <p>
          : <b>{person.address?.zip}</b>
        </p>
      </div>
    </div>
  );
};
export default UserInfo;
